

















































































import DatasetSelectorPopup from "./components/left-drawer/DatasetSelectorPopup.vue";
import Navbar from "@/components/left-drawer/Navbar.vue";
import RigidTileHome from "@/views/RigidTileHome.vue";
import Vue from "vue";
import { mapState } from "vuex";
import VueSimpleAlert from "vue-simple-alert";
import { ButtonPlugin } from "@syncfusion/ej2-vue-buttons";
import { enableRipple } from "@syncfusion/ej2-base";
import Multiselect from "vue-multiselect";
// import loading from 'vue-full-loading'

Vue.use(VueSimpleAlert);
enableRipple(true);
Vue.use(ButtonPlugin);

import { EntityType, SelectionAction, VisualizationType } from "./types";
export default Vue.extend({
    name: "App",
    components: {
        DatasetSelectorPopup,
        Navbar,
        RigidTileHome,
        Multiselect
    },
    data() {
        return {
            load_dialog: false,
            value_classifier: null
        };
    },
    mounted() {
        this.$store.dispatch("loadFirstDataset");
        window.addEventListener("keydown", e =>
            this.$store.dispatch("changedToggleShift", e)
        );
        window.addEventListener("keyup", e =>
            this.$store.dispatch("changedToggleShift", e)
        );
    },
    computed: {
        helpButtons(): any[] {
            return [
                { name: "Boxer Overview" },
                { name: "Boxer Views Description" },
                { name: "Boxer Demo Introduction" }
            ];
        },
        viewsMapping(): any[] {
            if (this.datasetType == "continuous") {
                return [
                    {
                        view: VisualizationType.CA,
                        view_name: "Performance (Overall)"
                    },
                    {
                        view: VisualizationType.SCP,
                        view_name: "Performance (Selection)"
                    },
                    {
                        view: VisualizationType.CC,
                        view_name: "Performance (Curves)"
                    },
                    {
                        view: VisualizationType.RC,
                        view_name: "Reliability (Curves)"
                    },
                    {
                        view: VisualizationType.CB,
                        view_name: "(Trinary) Performance Confidence"
                    },
                    {
                        view: VisualizationType.BA,
                        view_name: "(Trinary) Bandwidth Assessment"
                    },
                    {
                        view: VisualizationType.REC,
                        view_name: "(Trinary) Rejected Curve"
                    },
                    {
                        view: VisualizationType.TID,
                        view_name: "(Trinary) Instance Distribuion"
                    },
                    {
                        view: VisualizationType.UM,
                        view_name: "Uncertainty HeatMap"
                    },
                    { view: VisualizationType.SP, view_name: "Scatter Plot" },
                    {
                        view: VisualizationType.FH,
                        view_name: "Feature Histogram"
                    },
                    {
                        view: VisualizationType.CMG,
                        view_name: "Confusion Matrix Grid"
                    },
                    {
                        view: VisualizationType.PCC,
                        view_name: "Pairwise Classifier Consensus"
                    },
                    {
                        view: VisualizationType.OCA,
                        view_name: "Cumulative Accuracy"
                    },
                    { view: VisualizationType.SM, view_name: "Metrics Table" },
                    {
                        view: VisualizationType.SMP,
                        view_name: "Metrics Parallel"
                    },
                    { view: VisualizationType.ITL, view_name: "Instance List" },
                    { view: VisualizationType.FI, view_name: "Focus Item" }
                ];
            }
            return [
                {
                    view: VisualizationType.CA,
                    view_name: "Performance (Overall)"
                },
                {
                    view: VisualizationType.SCP,
                    view_name: "Performance (Selection)"
                },
                {
                    view: VisualizationType.COV,
                    view_name: "Performance (Per Class)"
                },
                { view: VisualizationType.FH, view_name: "Feature Histogram" },
                {
                    view: VisualizationType.CMG,
                    view_name: "Confusion Matrix Grid"
                },
                {
                    view: VisualizationType.PCC,
                    view_name: "Pairwise Classifier Consensus"
                },
                {
                    view: VisualizationType.OCA,
                    view_name: "Cumulative Accuracy"
                },
                { view: VisualizationType.SM, view_name: "Metrics Table" },
                { view: VisualizationType.SMP, view_name: "Metrics Parallel" },
                { view: VisualizationType.ITL, view_name: "Instance List" },
                { view: VisualizationType.FI, view_name: "Focus Item" }
            ];
        },
        classifiers(): string[] {
            return [...this.$store.getters.classifiers];
        },
        classes(): string[] {
            return [...this.$store.getters.classes];
        },
        views(): string[] {
            return this.$store.state.views.all;
        },
        corsError() {
            return !this.$store.state.corsError;
        },
        ...mapState(["chosenDataset"]),
        snackbar() {
            return !this.$store.getters.intersectionMode;
        },
        datasetType(): string {
            return this.$store.state.datasetType;
        }
    },
    watch: {
        value_classifier() {
            const payload: {
                entityType: EntityType;
                newFilter: {
                    type: SelectionAction;
                    set: Set<string>;
                };
            } = {
                entityType: EntityType.Classifier,
                newFilter: {
                    type: SelectionAction.Exclude,
                    set: new Set(this.value_classifier)
                }
            };
            this.$store.dispatch("changedFilter", payload);
            if (this.datasetType == "continuous") {
                this.$store.dispatch("changedContinuousMetrics");
            }
        },
        chosenDataset() {
            this.load_dialog = false;
        },
        corsError() {
            this.$alert(
                "Dataset load failed. Please make sure turn on the CORS extension"
            );
        }
    },
    methods: {
        btnClick(name) {
            if (name == "Git Repo") {
                window.open("https://github.com/uwgraphics/Boxer/");
            } else if (name == "Related Use case") {
                let url = this.$store.state.chosenFixedDataset.related_use_case;
                window.open(url);
            } else if (name == "Boxer Views Description") {
                window.open(
                    "https://pages.graphics.cs.wisc.edu/BoxerDocs/docs/user_guide/view_description/"
                );
            } else if (name == "Boxer Demo Introduction") {
                window.open(
                    "https://pages.graphics.cs.wisc.edu/BoxerDocs/docs/publication/demo/"
                );
            } else {
                window.open("https://pages.graphics.cs.wisc.edu/BoxerDocs/");
            }
        },
        change_grid_size() {
            this.$store.dispatch("changedGridSize");
        },
        save_views() {
            this.$store.dispatch("saveViews");
            this.$store.dispatch("wrtieConfig");
        },
        reload_views() {
            this.$store.dispatch("reloadViews");
        },
        open_repo() {
            this.$store.dispatch("openRepo");
        },
        resume_views() {
            this.$store.dispatch("resumeViews");
        },
        buttonPush(b) {
            console.log("DOWN", b);
        },
        buttonUp(b) {
            console.log("UP", b);
        },
        toggleIsClosed() {
            const nav = document.getElementById("nav-1");
            if (nav === null) {
                return;
            }
            nav.classList.toggle("is-closed");
        }
    }
});
