








































import Vue from "vue";
import { mapActions } from "vuex";
import VueInstantLoadingSpinner from "vue-instant-loading-spinner/src/components/VueInstantLoadingSpinner.vue";

export default Vue.extend({
    name: "DatasetSelectorPopup",
    components: {
        VueInstantLoadingSpinner
    },
    data() {
        return {
            customDatasetURL: ""
        };
    },
    methods: {
        btnClick() {
            console.log("click", this.$store.state.datasets);
            window.open(
                "https://pages.graphics.cs.wisc.edu/BoxerDocs/docs/user_guide/data_preparation/"
            );
        },
        test: function() {
            console.log("call test");
            this.$refs.Spinner.show();
            setTimeout(
                function() {
                    this.$refs.Spinner.hide();
                }.bind(this),
                5000
            );
        },
        ...mapActions(["changedDataset", "changedDatasetByName"])
    }
});
